export function escapeRegExp (string) {
  return string.replace(/[.*+?^${}()/|[\]\\]/g, '\\$&')
}

export function splitOnLineBreaks (string) {
  return string.split(/(\r\n|\n|\r)/gm)
}

export function deepCopy (obj) {
  return JSON.parse(JSON.stringify(obj))
}
